import React, { useEffect } from 'react';
import Button from '../../components/button/Button'
import { NavLink } from 'react-router-dom';
import './confidentialite.css';

const Confidentialite = () => {
    useEffect(() => {
        window.scrollTo(0, 0);
        return () => {
            window.scrollTo(0, 0);
        };
    }, []);

    return (
        <div >
            <div >
                <h1>Politique de confidentialit</h1>
            </div>
            <div className='confidentialite'>

                <p>Votre vie prive est importante pour nous. Cette politique de confidentialit explique comment nous collectons, utilisons, divulguons et protgeons vos informations.</p>

                <h2>Collecte d'informations</h2>
                <p>Nous collectons des informations lorsque vous remplissez notre formulaire de contact. Les informations collectes incluent votre nom, votre adresse e-mail, votre numro de tlphone, votre entreprise et votre message.</p>

                <h2>Utilisation des informations</h2>
                <p>Les informations que nous collectons lors de l'utilisation du formulaire de contact sont utilises uniquement dans le but de rpondre  votre demande et de vous fournir les informations demandes.</p>

                <h2>Divulgation  des tiers</h2>
                <p>Nous ne vendons, n'changeons ni ne transfrons vos informations personnelles identifiables  des tiers. Cela n'inclut pas les tiers de confiance qui nous aident  exploiter notre site Web ou  mener nos activits, tant que ces parties acceptent de garder ces informations confidentielles.</p>

                <h2>Protection des informations</h2>
                <p>Nous mettons en uvre une varit de mesures de scurit pour garantir la scurit de vos informations personnelles. Nous utilisons des protocoles de communication scuriss (HTTPS) pour protger la transmission de vos informations, et nous utilisons un stockage scuris pour protger les informations que nous collectons.</p>

                <h2>Cookies</h2>
                <p>Nous n'utilisons pas de cookies sur notre site Web.</p>

                <h2>Consentement</h2>
                <p>En utilisant notre formulaire de contact, vous consentez  notre politique de confidentialit.</p>

                <h2>Modifications de la politique de confidentialit</h2>
                <p>Toute modification apporte  notre politique de confidentialit sera publie sur cette page.</p>

                <p>Si vous avez des questions ou des proccupations concernant notre politique de confidentialit, veuillez nous contacter  "hwweb21@gmail.com.</p>
            </div>
            <div className="confidentialite-button">
                <Button  >
                    <NavLink to="/contact" >Fermer</NavLink>
                </Button>
            </div>
        </div>
    );
};

export default Confidentialite;