import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './competence.css';

const Competence = ({ nom, description, icone }) => {
    // tat d'activit de la comptence
    const [isActive, setIsActive] = useState(false);

    // Fonction pour basculer l'tat d'activit de la comptence
    const toggleDescription = () => {
        setIsActive(!isActive);
    };

    // Rendu du composant Competence
    return (
        <div className={`competence ${isActive ? 'active' : ''}`}>
            <div className="competence-content">
                {/* Lien vers la page Realisations avec tri sur 3 caractres du nom de la comptence */}
                <Link to={`/realisations#${nom.toLowerCase().substring(0, 3)}`} style={{ textDecoration: 'none' }}>
                    {/* Affiche le nom de la comptence et son icne */}
                    <span className="nom"><img src={icone} alt={nom} />{nom}</span>
                </Link>
                {/* Utilise la classe CSS en fonction de l'tat d'activit et ajoute une classe pour la position de la flche */}
                <div
                    onClick={toggleDescription}
                    className={`arrow ${isActive ? 'arrow-up' : 'arrow-down'} ${isActive ? 'arrow-up-active' : 'arrow-down-active'}`}
                ></div>
                {/* Affiche la description si la comptence est active */}
                {isActive && (
                    <div className="description">
                        {/* Map  travers les lignes de description et les affiche en tant que paragraphes */}
                        {description.map((descriptionLine, index) => (
                            <p key={index}>{descriptionLine}</p>
                        ))}
                        {/* Bouton pour ouvrir la page Realisations avec les lments associs  la comptence */}
                        <Link to={`/realisations#${nom.toLowerCase().substring(0, 3)}`}>
                            <button className='look-button'>Voir mes ralisations utilisant {nom}</button>
                        </Link>
                    </div>
                )}
            </div>
        </div>
    );
};

export default Competence;
