// ModalComponent.js
import React from 'react';
import Modal from 'react-modal';
import './modale.css';

// Dfinit l'lment racine pour l'accessibilit du composant Modale
Modal.setAppElement('#root');

// Composant ModalComponent
function ModalComponent({ isOpen, closeModal, message, resetForm }) {
    // Fonction pour fermer la modale et rinitialiser le formulaire si ncessaire
    const handleCloseModal = () => {
        closeModal();
        // Rinitialise le formulaire si la fonction resetForm est fournie
        if (resetForm) {
            resetForm();
        }
    };

    // Rendu du composant ModalComponent
    return (
        <Modal
            isOpen={isOpen}
            onRequestClose={handleCloseModal}
            contentLabel="Example Modal"
            className="custom-modal"
        >
            {/* Affiche le message de la modale */}
            <p>{message}</p>
            {/* Bouton pour fermer la modale */}
            <button onClick={handleCloseModal}>Fermer</button>
        </Modal>
    );
}

export default ModalComponent;
