import React from 'react';
import Video from '../../components/video/Video';
import './mediaWrapper.css';


const MediaWrapper = ({ mediaList }) => {

    return (
        // Conteneur principal pour les vidos et images
        <div className="videos-container">
            {/* Mapper  travers la liste de mdias */}
            {mediaList.map((media, index) => (
                // Vrifier si le mdia est une image
                'image' === media.type ? (
                    // Si c'est un lment React (image), le rendre tel quel
                    <div key={index} className="imageContain">
                        <img
                            src={media.url}
                            alt={media.description}
                        />
                    </div>
                ) : (
                    // Si c'est une chane (URL vido), rendre le composant Video
                    <Video key={index} url={media.url} alt={media.description} />
                )
            ))}
        </div>
    );
};

// Exporter le composant MediaWrapper
export default MediaWrapper;
